@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Lato', sans-serif;
  font-size: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}
aside{
    width: 250px;
    overflow: scroll;
    overflow-x: hidden;
    transition: 0.4s;
}

ul.nav li{
    border: solid 2px #f1f1f1;
    border-right: none;
    margin-top: 20%;
    padding: 5px 0px;
    transition: all 0.6s;
}

ul.nav li:hover, ul.nav{
    border-color: #2d3436;
    transition: all 1s;
}

ul.nav li button{
    cursor: pointer;
    color: #2d3436;
    margin-left: 20px;
    font-weight: 600;
    background-color: transparent;
    border: none;
}

#hamburguer{
    margin-left: 34px;
    margin-top: 20px;
    background-color: transparent;
    border: none;
}

#hamburguer-icon{
    cursor: pointer;
}

#foursquareImg{
    width: 200px;
}

.hidden{
    overflow: hidden;
    width: 80px;
    transition: 0.4s;
}

.hidden ul{
    display: none;
    margin-left: -100px;
    transition: 0.4s;
}

#filter{
    margin-top: 30px;
    border: solid 2px #2d3436;
    border-right: none;
    transition: all 0.6s;
    padding:10px;
    text-align: center;
}

#filter::-webkit-input-placeholder{
    text-align: center;
}

#filter::-ms-input-placeholder{
    text-align: center;
}

#filter::placeholder{
    text-align: center;
}

.fa-arrows-alt{
    margin-right: 10px;
}

#error-h1{
    width: 100%;
    text-align: center;
}
